@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box;background-color:#f8c28c;font-family:'Poppins', sans-serif}.App{min-height:100vh}.App-header h2{display:flex;justify-content:center;padding-top:5vh;align-items:center;font-weight:200;color:#902211}.App-header h1{display:flex;justify-content:center;margin-bottom:10vh;align-items:center;color:#230e0b}.main{display:flex;flex-direction:column}.main .woman-wrapper{display:flex;flex-direction:column;align-items:center;margin-bottom:1rem}.main #woman{height:300px;width:300px;position:relative;-webkit-animation:mymove 5s ease infinite;animation:mymove 5s ease infinite}.main .ellipse{background-color:#f8b186;height:50px;width:200px;border-radius:50%;-webkit-animation:shadow 5s ease-out infinite;animation:shadow 5s ease-out infinite}.bottom{display:flex;margin-left:22vw}.audio{display:flex;justify-content:center}@-webkit-keyframes mymove{from{top:0px}50%{top:-50px}to{top:0px}}@keyframes mymove{from{top:0px}50%{top:-50px}to{top:0px}}@-webkit-keyframes shadow{from{width:200px}50%{width:150px}to{width:200px}}@keyframes shadow{from{width:200px}50%{width:150px}to{width:200px}}

.TrackList{border-radius:20px;box-shadow:-6px -6px 10px rgba(255,255,255,0.1),6px 6px 10px rgba(0,0,0,0.1);padding:1.5rem;width:20rem;display:flex;align-items:center;justify-content:center;margin:auto}.toggle{display:flex}button{position:static;width:100px;position:relative;background:#f8c28c;border-radius:100px;text-align:center;border:none;margin:.5rem;cursor:pointer;transition:all 0.1s ease-in-out;color:white}button span{line-height:50px;font-size:25px}button:hover{opacity:0.3;box-shadow:-6px -6px 10px rgba(255,255,255,0.2),6px 6px 10px rgba(0,0,0,0.2)}button:active{opacity:1;box-shadow:inset -4px -4px 8px rgba(255,255,255,0.5),inset 8px 8px 16px rgba(0,0,0,0.1);color:#f8cda1}

